___CSS_LOADER_EXPORT___.push([module.id, `*{
    box-sizing: border-box;
}

body{
    margin: 0;
    padding: 0;
    font-family: "Poppins", sans-serif;
    background-color: #232023;
    color: rgb(213, 213, 213);
    display: flex;
    flex-direction: column;
    height: 100vh;
}

#logoImg{
    width: 110px;
    border-radius: 100px;
    margin-left: 2vw;
}

#header{
    display: flex;
    align-items: center;
    font-size: clamp(28px, 5vw, 38px);
    font-weight: 700;
    background-color: #5a5a5a;
    gap: 10px;
}


#main{
    display: flex;
    flex-grow: 1;
    background-color: #232023;
}


#footer{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    font-weight: 600;
    background-color: #5a5a5a;
    font-size: clamp(14px, 1vw, 18px);
    padding: 10px;
}
#footer img{
    width: 25px;
    height: 25px;
    cursor: pointer;
    border-radius: 40px;
}
#footer img:hover{
    box-shadow: 0 0 15px white;
    transform: scale(1.1);
}
